<!--
 * @Description: 编辑弹窗
 * @Autor: YML
 * @Date: 2021-05-14 11:53:32
 * @LastEditors: YML
 * @LastEditTime: 2021-05-25 11:18:32
-->
<template>
  <div class="edit-modal-three">
    <modal
      width="58.65vw"
      height="35.31vw"
      :title="title"
      @close="$emit('close')"
      :visible="visible"
    >
      <img
        slot="modalBg"
        class="img-edit"
        src="../../assets/img/components/img-edit.png"
        alt=""
      />
      <a-form-model
        layout="inline"
        class="form-edit"
        ref="ruleForm"
        :model="form"
        :rules="rules"
      >
        <a-form-model-item label="姓名：" prop="name">
          <a-input
            class="default-input"
            placeholder="请输入姓名"
            :disabled="isShow"
            v-model="form.name"
          />
        </a-form-model-item>
        <a-form-model-item label="性别：" prop="sex">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择性别"
            :disabled="isShow"
            v-model="form.sex"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              男
            </a-select-option>
            <a-select-option value="2">
              女
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="证件号码：" prop="identity">
          <a-input
            class="default-input"
            placeholder="请输入证件号码"
            :disabled="isShow"
            v-model="form.identity"
          />
        </a-form-model-item>
        <a-form-model-item label="家庭住址：" prop="familyAddress">
          <a-input
            class="default-input"
            placeholder="请输入家庭住址"
            :disabled="isShow"
            v-model="form.familyAddress"
          />
        </a-form-model-item>
        <a-form-model-item label="现住地：" prop="currentResidence">
          <a-input
            class="default-input"
            placeholder="请输入现住地"
            :disabled="isShow"
            v-model="form.currentResidence"
          />
        </a-form-model-item>
        <a-form-model-item label="等级：" prop="grade">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择等级"
            :disabled="isShow"
            v-model="form.grade"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              等级1
            </a-select-option>
            <a-select-option value="2">
              等级2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="类别：" prop="type">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择类别"
            :disabled="isShow"
            v-model="form.type"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              类别1
            </a-select-option>
            <a-select-option value="2">
              类别2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="省份：" prop="province">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择省份"
            :disabled="isShow"
            v-model="form.province"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              省份1
            </a-select-option>
            <a-select-option value="2">
              省份2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <!-- <a-form-model-item label="选择时间：" prop="time">
          <a-range-picker
            separator=""
            show-time
            dropdown-class-name="default-range-picker-dropdown"
            class="default-range-picker"
            :disabled="isShow"
            :get-calendar-container="triggerNode => triggerNode.parentNode"
          />
        </a-form-model-item> -->
        <a-form-model-item label="开始时间：" prop="startTime">
          <a-date-picker
            class="default-date-picker"
            show-time
            dropdown-class-name="default-date-picker-dropdown"
            :disabled="isShow"
            :get-calendar-container="triggerNode => triggerNode.parentNode"
          />
        </a-form-model-item>
        <a-form-model-item label="结束时间：" prop="endTime">
          <a-date-picker
            class="default-date-picker"
            show-time
            dropdown-class-name="default-date-picker-dropdown"
            :disabled="isShow"
            :get-calendar-container="triggerNode => triggerNode.parentNode"
          />
        </a-form-model-item>
        <a-form-model-item label="签发机关：" prop="issuingAuthority">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择签发机关"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              签发机关1
            </a-select-option>
            <a-select-option value="2">
              签发机关2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="管控机关：">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择管控机关"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              管控机关1
            </a-select-option>
            <a-select-option value="2">
              管控机关2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="管控联系人：">
          <a-input
            class="default-input"
            placeholder="请输入管控联系人"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="联系人电话：">
          <a-input
            class="default-input"
            placeholder="请输入联系人电话"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="备注">
          <a-textarea
            class="default-textarea"
            placeholder="请进行拦截"
            :disabled="isShow"
          />
        </a-form-model-item>
      </a-form-model>
      <div class="line" v-show="!isShow"></div>
      <!-- 取消和保存按钮 start -->
      <div class="operation-btn" v-show="!isShow">
        <a-button type="default" class="form-button" @click="$emit('close')"
          >取消</a-button
        >
        <a-button type="primary" class="form-button save-button" @click="save"
          >保存</a-button
        >
      </div>
      <!-- 取消和保存按钮 end -->
    </modal>
  </div>
</template>

<script>
import Modal from "../../components/Modal";
export default {
  components: {
    Modal //弹窗组件
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      form: {
        name: "", //姓名
        sex: undefined, //性别
        identity: "", //证件号码
        familyAddress: "", //家庭住址
        currentResidence: "", //现住地
        grade: undefined, //等级
        type: undefined, //类别
        province: undefined, //省份
        startTime: null, //开始时间
        endTime: null, //结束时间
        issuingAuthority: undefined //签发机关
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入姓名！",
            trigger: "blur"
          }
        ],
        sex: [
          {
            required: true,
            message: "请选择性别！",
            trigger: "blur"
          }
        ],
        identity: [
          {
            required: true,
            message: "请输入身份证号！",
            trigger: "blur"
          }
        ],
        grade: [
          {
            required: true,
            message: "请选择等级！",
            trigger: "blur"
          }
        ],
        type: [
          {
            required: true,
            message: "请选择类别！",
            trigger: "blur"
          }
        ]
      },
      isShow: this.title == "查看详情"
    };
  },
  methods: {
    save() {
      console.log(this.form);
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less"></style>
