 * @Description: 人员黑名单
 * @Autor: YML
 * @Date: 2021-05-08 09:20:20
 * @LastEditors: YML
 * @LastEditTime: 2021-05-25 15:48:05
-->
<template>
  <div class="person-blacklist">
    <box>
      <!-- 信息筛选 start -->
      <div class="search">
        <div class="search-body">
          <a-form-model
            layout="inline"
            class="search-main"
            ref="searchForm"
            :model="searchForm"
          >
            <a-form-model-item label="部门：" prop="department">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                placeholder="请选上级部门"
                v-model="searchForm.department"
                :getPopupContainer="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="1">
                  部门1
                </a-select-option>
                <a-select-option value="2">
                  部门2
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="姓名：" prop="name">
              <a-input
                class="default-input"
                placeholder="请输入姓名"
                v-model="searchForm.name"
                allow-clear
              />
            </a-form-model-item>
            <a-form-model-item label="证件号码：" prop="identity">
              <a-input
                class="default-input"
                placeholder="请输入证件号码"
                v-model="searchForm.identity"
                allow-clear
              />
            </a-form-model-item>
            <a-form-model-item label="类别：" prop="type">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                placeholder="请选上级类别"
                v-model="searchForm.type"
                :getPopupContainer="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="1">
                  类别1
                </a-select-option>
                <a-select-option value="2">
                  类别2
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="等级：" prop="grade">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                placeholder="请选上级等级"
                v-model="searchForm.grade"
                :getPopupContainer="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="1">
                  等级1
                </a-select-option>
                <a-select-option value="2">
                  等级2
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="来源：" prop="source">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                placeholder="请选上级来源"
                v-model="searchForm.source"
                :getPopupContainer="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="1">
                  来源1
                </a-select-option>
                <a-select-option value="2">
                  来源2
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="开始时间：" prop="startTime">
              <a-date-picker
                class="default-date-picker"
                show-time
                v-model="searchForm.startTime"
                dropdown-class-name="default-date-picker-dropdown"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
            <a-form-model-item label="结束时间：" prop="endTime">
              <a-date-picker
                class="default-date-picker"
                show-time
                v-model="searchForm.endTime"
                dropdown-class-name="default-date-picker-dropdown"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
          </a-form-model>
        </div>
      </div>
      <!-- 信息筛选 end -->
      <!-- 编辑导航 start -->
      <div class="operation-nav">
        <div class="btn-box">
          <a-button class="operation-button">
            <span class="iconfont iconfont-14 icon-sousuo1"></span>
            <span class="btn-title">查询</span>
          </a-button>
          <a-button class="operation-button" @click="toReset">
            <span class="iconfont iconfont-18 icon-zhongzhi"></span>
            <span class="btn-title">重置</span>
          </a-button>
          <a-button class="operation-button" @click="addMessage">
            <span class="iconfont iconfont-16 icon-bianji2"></span>
            <span class="btn-title">新增</span>
          </a-button>
          <a-button class="operation-button">
            <span class="iconfont iconfont-14 icon-shanchu"></span>
            <span class="btn-title">批量删除</span>
          </a-button>
          <a-button class="operation-button">
            <span class="iconfont iconfont-14 icon-daoru-"></span>
            <span class="btn-title">导入</span>
          </a-button>
          <a-button class="operation-button">
            <span class="iconfont iconfont-14 icon-mobanxiazailiebiao"></span>
            <span class="btn-title">模板下载</span>
          </a-button>
        </div>
      </div>
      <!-- 编辑导航 end -->
      <!-- 表格展示 start -->
      <div class="contents">
        <a-table
          class="default-table"
          :columns="columns"
          :data-source="data"
          :pagination="false"
          :row-selection="rowSelection"
          :scroll="{ y: 'calc(90vh - 18.5vw)' }"
        >
          <div slot="operation" class="operation">
            <div class="count" @click="editMessage()">
              <span class="iconfont iconfont-18 icon-bianji"></span>
              <span class="title">编辑</span>
            </div>
            <div class="count" @click="getMessage()">
              <span class="iconfont iconfont-18 icon-yanjing"></span>
              <span class="title">查看</span>
            </div>
            <div class="count" @click="deleteMessage()">
              <span class="iconfont iconfont-16 icon-del"></span>
              <span class="title">删除</span>
            </div>
          </div>
        </a-table>
      </div>
      <!-- 表格展示 end -->
      <!-- 分页 start -->
      <div class="content-pagination">
        <pagination
          ref="pagination"
          :total="100"
          @change="onPaginationChange"
        />
      </div>
      <!-- 分页 end -->
    </box>
    <!-- 新增弹窗 start -->
    <editmodal
      :title="'新增信息'"
      :visible="addModal"
      @close="closeModal"
    ></editmodal>
    <!-- 新增弹窗 end -->
    <!-- 编辑弹窗 start -->
    <editmodal
      :title="'编辑信息'"
      :visible="editModal"
      @close="closeModal"
    ></editmodal>
    <!-- 编辑弹窗 end -->
    <!-- 查看弹窗 start -->
    <editmodal
      :title="'查看详情'"
      :visible="examineModal"
      @close="closeModal"
    ></editmodal>
    <!-- 查看弹窗 end -->
    <!-- 删除弹窗 start -->
    <deletemodal :visible="deleteModal" @close="closeModal"></deletemodal>
    <!-- 删除弹窗 end -->
  </div>
</template>

<script>
import Box from "../../components/Box";
import Pagination from "../../components/Pagination";
import editmodal from "./editModal";
import deletemodal from "../../components/deleteModal";
export default {
  components: {
    Box, //边框组件
    Pagination, //分页组件
    editmodal, //新增、编辑、查看弹窗
    deletemodal //删除弹框
  },
  data() {
    return {
      pageSize: 10, // 每页条数
      page: 1, // 当前页
      searchForm: {
        department: undefined, //部门
        name: "", //姓名
        identity: "", //证件号码
        type: undefined, //类别
        grade: undefined, //等级
        source: undefined, //来源
        time: null //时间
      },
      columns: [
        {
          title: "部门",
          dataIndex: "department",
          key: "department",
          width: "5vw"
        },
        {
          title: "姓名",
          dataIndex: "name",
          key: "name",
          width: "5vw"
        },
        {
          title: "性别",
          dataIndex: "sex",
          key: "sex",
          width: "4vw"
        },
        {
          title: "证件号码",
          dataIndex: "certificatesNum",
          key: "certificatesNum",
          width: "11vw"
        },
        {
          title: "类别",
          dataIndex: "type",
          key: "type",
          width: "5vw"
        },
        {
          title: "等级",
          dataIndex: "grade",
          key: "grade",
          width: "5vw"
        },
        {
          title: "来源",
          dataIndex: "source",
          key: "source",
          width: "7vw"
        },
        {
          title: "入库时间",
          dataIndex: "warehousingTime",
          key: "warehousingTime",
          width: "10vw"
        },
        {
          title: "操作",
          dataIndex: "operation",
          key: "operation",
          width: "12vw",
          scopedSlots: { customRender: "operation" }
        }
      ], //表头信息
      data: [],
      selectedRowKeys: [], //表格内容
      // visible: true,
      addModal: false, //新增操作弹窗是否显示
      editModal: false, //编辑操作弹窗是否显示
      examineModal: false, //查看操作弹窗是否显示
      deleteModal: false //删除操作弹窗是否显示
    };
  },
  computed: {
    /**
     * 列表项是否可选择
     */
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        selectedRowKeys,
        onChange: this.onSelectChange,
        hideDefaultSelections: true
      };
    }
  },
  mounted() {
    //设置分页数据
    this.$refs.pagination.setData(this.page, this.pageSize);
    // 加载表格数据
    this.setList();
  },
  methods: {
    /**
     * 向列表项追加内容
     */
    setList() {
      let data = [];
      for (let i = 0; i < this.pageSize; i++) {
        let item = {
          key: i,
          department: "市公安局",
          name: `张三${i}`,
          sex: "男",
          certificatesNum: "342101188956598756",
          type: "涉恐人员",
          grade: "等级一",
          source: "检查站添加",
          warehousingTime: "2021-05-08 10:46:20"
        };
        data.push(item);
      }
      this.data = data
    },
    /**
     * 点击重置按钮
     */
    toReset() {
      this.$refs.searchForm.resetFields();
    },
    /**
     * 列表项选中状态
     */
    onSelectChange(selectedRowKeys) {
      console.log("selectedRowKeys changed: ", selectedRowKeys);
      this.selectedRowKeys = selectedRowKeys;
    },
    /**
     * 点击×号关闭弹框
     */
    closeModal() {
      this.addModal = false;
      this.editModal = false;
      this.examineModal = false;
      this.deleteModal = false;
    },
    /**
     * 点击新增打开弹窗
     */
    addMessage() {
      this.addModal = true;
    },
    /**
     * 点击编辑打开弹窗
     */
    editMessage() {
      this.editModal = true;
    },
    /**
     * 点击查看打开弹窗
     */
    getMessage() {
      this.examineModal = true;
    },
    /**
     * 点击删除打开弹窗
     */
    deleteMessage() {
      this.deleteModal = true;
    },
    /**
     * 分页事件
     */
    onPaginationChange(page, pageSize) {
      console.log(page, pageSize);
      this.pageSize = pageSize;
      this.setList();
    },
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
